import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-food',
  templateUrl: './food.component.html',
  styleUrls: ['./food.component.css']
})
export class FoodComponent implements OnInit {
  visible: Boolean;
  constructor() { }

  ngOnInit() {
    this.visible = true;
  }
  showMessage() {
    this.visible = false;
  }
}



